.promo {
  position: relative;
  background-color: #f9f9f9;
}

.promo__wrapper {
  background-image: url("../img/hero-mobile@1x.jpg");
  background-color: #f9f9f9;
  background-size: 575px 373px;
  background-position: center -15%;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  margin-bottom: 0;

  @media (min-resolution: 144dpi),
    (min-resolution: 1.5dppx) {
    background-image: url("../img/hero-mobile@2x.jpg");
  }

  @media (min-width: $tablet-width) {
    background-image: url("../img/hero-tablet@1x.jpg");
    background-size: 1150px 746px;
    background-position: center bottom;
    min-height: 746px;

    @media (min-resolution: 144dpi),
      (min-resolution: 1.5dppx) {
      background-image: url("../img/hero-tablet@2x.jpg");
    }
  }

  @media (min-width: $desktop-width) {
    background-image: url("../img/hero-desktop@1x.jpg");

    @media (min-resolution: 144dpi),
      (min-resolution: 1.5dppx) {
      background-image: url("../img/hero-desktop@2x.jpg");
    }
  }
}

.promo__title {
  font-size: 27px;
  font-weight: bold;
  text-align: center;
  padding-top: 50px;
  margin-bottom: 106px;
  padding-left: 50px;
  padding-right: 50px;

  @media (min-width: $tablet-width) {
    width: 500px;
    margin-left: auto;
    margin-right: auto;
    font-size: 43px;
    line-height: 50px;
    padding-top: 115px;
    margin-bottom: 0;
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 375px;
  }

  @media (min-width: $desktop-width) {
    font-size: 53px;
    width: 700px;
    line-height: 57px;
    padding-top: 108px;
    padding-bottom: 359px;
  }
}

.promo__list {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  padding: 0;
  margin-bottom: 0;

  @media (min-width: $tablet-width) {
    max-width: 670px;
    margin: 0 auto;
  }

  @media (min-width: $desktop-width) {
    max-width: 1050px;
    margin: 0 auto;
  }
}

.promo__list-link {
  display: block;
  color: white;
  text-decoration: none;
  font-size: 19px;
  line-height: 24px;
  font-weight: bold;
  width: 200px;
  padding-top: 26px;
  padding-left: 57px;
  padding-right: 30px;
  transition: opacity 0.3s ease;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
    padding-top: 28px;
    padding-left: 36px;
  }
}

.promo__list-item {
  position: relative;
  list-style: none;
  min-height: 103px;
  width: 100%;
  transition: transform 0.3s ease;

  &:hover {
    transform: translateY(-5px);
  }

  &:hover .promo__icon {
    opacity: 1;
  }

  &:active .promo__icon,
  &:active .promo__list-link {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    width: 50%;
    min-height: 121px;
  }

  @media (min-width: $desktop-width) {
    min-height: 122px;
  }
}

.promo__list-item--interior {
  background-color: #63d1bb;

  &:hover {
    background-color: #59d5bc;
  }
}

.promo__list-item--toy {
  background-color: #56c3b2;

  &:hover {
    background-color: #46c1ae;
  }
}


.promo::after {
  @media (min-width: $tablet-width) {
    content: "";
    position: absolute;
    background-image: url("../img/bg-zigzag-fill.svg");
    height: 15px;
    background-size: 768px 24px;
    left: 0;
    right: 0;
    bottom: 0;
  }

  @media (min-width: $desktop-width) {
    content: "";
    position: absolute;
    background-image: url("../img/bg-zigzag-fill.svg");
    height: 21px;
    background-size: auto 37px;
    background-position-x: -10px;
    left: 23px;
    right: 0;
    bottom: 0;
    background-repeat: repeat-x;
  }
}

.promo__icon {
  position: absolute;
  fill: #149080;
  opacity: 0.5;
  transition: opacity 0.3s ease;
}

.promo__icon--interior {
  top: 23px;
  left: 72.5%;

  @media (min-width: $tablet-width) {
    top: 31px;
    left: 77.5%;
  }

  @media (min-width: $desktop-width) {
    top: 31px;
    left: 85.5%;
  }
}

.promo__icon--toy {
  top: 20px;
  left: 69.5%;

  @media (min-width: $tablet-width) {
    top: 31px;
    left: 71.5%;
  }

  @media (min-width: $desktop-width) {
    top: 33px;
    left: 82%;
  }
}
